
/*projects-all-outer--------------------------------------------------*/
.projects-all-outer{
	padding: 6vw 0vw 0vw 4vw;
}
/*projects-outer------------------------------------------------------*/
.projects-content-box-outer{
	margin: 0vw 0vw 6vw 0vw;
}

/*projects-preview-box------------------------------------------------*/
.projectsPreviewBox{
/*	border: 1px solid black;*/
box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
	padding: 9vw 0vw;
	background-repeat: no-repeat;
	background-position: top;
	background-size: 100% 100%;
}
.projectsPreviewBox button{
	display: block;
	margin: auto;
	font-family: 'Poppins',sans-serif;
	border: none;
	font-size: 1.2vw;
	background-color: black;
	color: white;
	padding: .8vw 1.8vw;
	border-radius: 2vw;
	box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.projectsPreviewBox button:hover{
	background-color: #5e17eb;
}
.projectsPreviewBox button a{
	display: block;
	width: 100%;
	height: 100%;
}
.QR-preview-box{
	background-image: url("../img/QR_img/QR1.jpeg");

}

.LP-preview-box{
	background-image: url("../img/LP_img/LP1.jpeg");
}

.RM-preview-box{
	background-image: url("../img/RM_img/RM1.jpeg");	
}
/*projects-img-box----------------------------------------------------*/
.projects-img-box.skills-img-box{
	background-image: url('../img/pProjects.png');
	background-size: 43vw 34vw;
}
.projects-img-box.skills-img-box h1{
	font-size: 6.5vw;
}


/*projets-content---------------------------------------------------*/
.projects-content-box{
	padding: 0vw 0vw 0vw 2vw;
	position: relative;
}
.projects-content-box::before{
	content: "";
	position: absolute;
	height: 100%;
	width: .3%;
	left: 1vw;
	top: 0;
	background-color: #5e17eb;
	background-color: black;
}
.projects-content-box:hover.projects-content-box::before{
	background-color: #5e17eb;
}
.projects-content-box:hover.projects-content-box h1{
	color: #5e17eb;
}
.projects-content-box span{
	color: #5e17eb;
	color: black;
	font-family: 'Poppins',sans-serif;
}
.projects-content-box h1{
	font-family: 'Poppins',sans-serif;
	font-size: 2.5vw;
	color: #5e17eb;
	color: black;
}
.projects-content-box h4{
	font-family: 'Poppins',sans-serif;
	font-weight: 400;
	font-size: 1.3vw;
	color: black;
	color: #5e17eb;

}
.projects-content-box a{
	color: #fc9808;
	color: red;
}

/*projests-preview-model-------------------------------------------------------*/
.modal-header h1{
	color: #5e17eb;
	font-family: 'Poppins',sans-serif;
	font-size: 2.5vw;
	font-weight: 600;
}

.carousel-items-cover{
	height: 33vw;
}
.carousel-items-cover .carousel-item{
	height: 100%;
}
.carousel-items-cover img{
	display: block;
	height: 100%;
	width: 100%;
}

button.carousel-control-btn{
	display: inline-block;
	background-color: transparent;
	color: black;
	margin: .5vw 20%;
}


/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
	
/*project outer-----------------------------------------------*/
	.skill-outer-box.projects-content-box-outer{
	padding: 0vw 0vw 0vw 4vw;

	}

	/*projects img -----------------------------------------------------*/
	.projects-img-box.skills-img-box{
	height: 170vw;
    margin: 0vw 0vw 0vw 0vw;
	padding: 6vw 0vw 6vw 4.4vw;
    background-size: 100% 88vw;
    background-position: right bottom;
	}
	.projects-img-box.skills-img-box h1{
    font-size: 13vw;
	}

/*project-preview-----------------------------------------------------------*/
	.projectsPreviewBox{
		height: 60vw;
		padding: 24vw 0vw;
	}
	.projectsPreviewBox button{
	font-size: 5vw;
	padding: 2vw 6vw;
	margin: 2vw auto;
	}
	/*	projects-content-----------------------------------------------------*/
	.projects-all-outer{
		padding: 8vw 0vw 0vw 4vw;
	}
	.projects-content-box-outer{
		margin: 0vw 0vw 12vw 0vw;
	}
	.projects-content-box{
		margin: 3vw 0vw;
	}
	.projects-content-box::before{
		left: 0;
		width: .5vw;
	}
	.projects-content-box h1{
	font-size: 6vw;
	}
	.projects-content-box h4{
	font-size: 4vw;
	}

	/*projests-preview-model-------------------------------------------------------*/
	.modal-header h1{
		font-size: 6.5vw;
	}
	.carousel-items-cover{
		height: 100vw;
	}
	button.carousel-control-btn{
		margin: 2vw 14%;
	}


}


